<template>
  <div class="container" style="display: flex; align-items: center">
    <div class="card o-hidden border-0 shadow-lg my-5">
      <div class="card-body p-0">
        <div class="row">
          <div class="col-lg-6 d-none d-lg-block">
            <img
              alt=""
              src="../assets/img/LOGO.png"
              width="100%"
              height="100%"
            />
          </div>
          <div class="col-lg-6">
            <div class="p-5">
              <div class="text-center">
                <h1 class="h4 text-gray-900 mb-4">创建一个账户!</h1>
              </div>
              <form class="user" @submit.prevent="register">
                <div class="form-group row">
                  <div class="col-sm-12 mb-3 mb-sm-0">
                    <input
                      type="text"
                      class="form-control form-control-user"
                      id="exampleFirstName"
                      placeholder="用户名"
                      v-model="userData.username"
                      @blur="checkUsername"
                    />
                    <p v-show="usernameExists">用户名已经存在！</p>
                  </div>
                </div>
                <div class="form-group">
                  <input
                    type="email"
                    class="form-control form-control-user"
                    id="exampleInputEmail"
                    placeholder="邮箱地址"
                    v-model="userData.email"
                    @blur="checkEmail"
                  />
                  <p v-show="emailExists">邮箱已经存在！</p>
                </div>
                <div class="form-group row">
                  <div class="col-sm-6 mb-3 mb-sm-0">
                    <input
                      type="password"
                      class="form-control form-control-user"
                      id="exampleInputPassword"
                      placeholder="密码"
                      v-model="userData.password"
                    />
                  </div>
                  <div class="col-sm-6">
                    <input
                      type="password"
                      class="form-control form-control-user"
                      id="exampleRepeatPassword"
                      placeholder="重复密码"
                      v-model="confirmPassword"
                      @blur="checkPassword"
                    />
                    <p v-show="!passwordSame">输入密码不一致!</p>
                  </div>
                </div>
                <button
                  type="submit"
                  class="btn btn-primary btn-user btn-block"
                >
                  注册账户
                </button>
              </form>
              <hr />
              <div class="text-center">
                <router-link
                  to="/forgot-password"
                  class="small"
                  href="forgot-password.html"
                  >忘记密码?</router-link
                >
              </div>
              <div class="text-center">
                <router-link to="/login" class="small" href="login.html"
                  >已经有了一个账户?登录!</router-link
                >
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Register',
  data() {
    return {
      userData: {
        username: '',
        email: '',
        password: '',
        userType: 2,
      },
      confirmPassword: '',
      usernameExists: false,
      emailExists: false,
      passwordSame: true,
    }
  },
  methods: {
    //检查重复username
    checkUsername() {
      axios
        .post('${this.$app.url}/check-username', {
          username: this.userData.username,
        })
        .then((response) => {
          this.usernameExists = response.data.exists
        })
        .catch((error) => {
          console.error(error)
          alert('无法连接到数据库服务器！')
        })
    },
    checkEmail() {
      axios
        .post(`${this.$app.url}/check-email`, { email: this.userData.email })
        .then((response) => {
          this.emailExists = response.data.exists
        })
        .catch((error) => {
          console.error(error)
          alert('无法连接到数据库服务器！')
        })
    },
    //检查密码一致性
    checkPassword() {
      this.passwordSame = this.confirmPassword === this.userData.password
    },
    //注册
    register() {
      if (!this.userData.username) {
        alert('用户名不能为空！')
        return
      }
      if (!this.userData.password) {
        alert('密码不能为空！')
        return
      }
      if (this.usernameExists) {
        alert('用户名已存在！')
        return
      }
      if (this.emailExists) {
        alert('邮箱已存在！')
        return
      }
      if (!this.passwordSame) {
        alert('两次输入的密码不一致！')
        return
      }

      axios
        .post('${this.$app.url}/register', this.userData)
        .then((response) => {
          alert('注册成功！')
          this.$router.push({ name: 'login' }, () => {})
        })
        .catch((error) => {
          console.error(error.response.data)
          alert('无法连接到数据库服务器！')
        })
    },
  },
}
</script>

<style scoped>
p {
  color: red;
  margin-bottom: 0;
}
</style>
